import uniBadge from './uni-badge.vue'
export default {
  name: 'UniGridItem',
  components: { uniBadge },
  props: {
    // 类型：可选值，dot：圆点；badge：角标；image：图片
    marker: {
      type: String,
      default: ''
    },
    // 水平方向
    hor: {
      type: Number,
      default: 0
    },
    // 垂直方向
    ver: {
      type: Number,
      default: 0
    },
    // badge 下颜色类型，可选值：default（灰色）、primary（蓝色）、success（绿色）、warning(黄色)、error(红色)
    type: {
      type: String,
      default: ''
    },
    // badge 下显示内容，汉字最多为1个
    text: {
      type: String,
      default: ''
    },
    // badge 下 Badge 大小
    size: {
      type: String,
      default: 'normal'
    },
    // badge 下 是否无需背景颜色
    inverted: {
      type: Boolean,
      default: false
    },
    // image 下图片路径
    src: {
      type: String,
      default: ''
    },
    // image 下图片宽度 ，最大 为 100 。 默认为 30
    imgWidth: {
      type: Number,
      default: 30
    }
  },
  inject: ['grid'],
  data () {
    return {
      column: 0,
      showBorder: true,
      square: true,
      highlight: true,
      left: 0,
      top: 0,
      index: 0,
      openNum: 2,
      width: 0,
      borderColor: '#d0dee5'
    }
  },
  created () {
    this.column = this.grid.column
    this.showBorder = this.grid.showBorder
    this.square = this.grid.square
    this.highlight = this.grid.highlight
    this.top = this.hor === 0 ? this.grid.hor : this.hor
    this.left = this.ver === 0 ? this.grid.ver : this.ver
    this.borderColor = this.grid.borderColor
    this.index = this.grid.index++
  },








  onReady () {
    this.grid._getSize(width => {
      this.width = width
    })
  },

  methods: {
    _onClick () {
      // console.log('点击', this.index);
      this.grid.change({ detail: { index: this.index } })
    }
  }
}
